"use client";
import { useVerifyToken } from "@/tanstack/queries";

export default function ProfilePage() {
    const {data, isLoading} = useVerifyToken();
    console.log(data);

    if(data === 401) return <p>Unauthorized</p>
    if (isLoading) return <p>Loading...</p>
    return (
        <div>
        <h1>Profile</h1>
        
        </div>
    );
    }